.scroll-down {
  width: 24px;
  height: 36px;
  border: 2px solid var(--accent);
  background-color: white;
  padding: 4px 6px;
  border-radius: 20px;
  position: absolute;
  left: calc(50% - 12px);
  top: 90vh;
  animation: mouse-down 2s infinite;
  content-visibility: auto;
}
.scroll-down .wheel {
  background-color: var(--accent);
  height: 10px;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
}
.scroll-down .wheel:before,
.scroll-down .wheel:after {
  content: '';
  display: block;
  position: absolute;
  top: -1px;
  left: 0;
  right: 0;
  border-bottom: 2px solid white;
  opacity: 0;
  animation: scroll-down 1.4s infinite;
}
.scroll-down .wheel:after {
  animation-delay: 0.7s;
}
@keyframes scroll-down {
  from {
    transform: translateY(0);
    opacity: 0.2;
  }
  50% {
    opacity: 1;
  }
  to {
    transform: translateY(10px);
    opacity: 0.2;
  }
}
@keyframes mouse-down {
  from {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
  to {
    transform: translateY(0);
  }
}
